#end-of-game {
  text-align: center;
}

#end-of-game h2 {
  font-size: 28pt;
}

#end-of-game p {
  font-size: 16pt;
}

#sorting-hat {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 50px;
}

.gold {
  color: gold;
}

.hat-wrapper img {
  height: 200px;
}

.results-wrapper {
  -ms-flex-item-align: center;
      align-self: center;
  background: black;
  border: 1px #fff solid;
  border-radius: 10px;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 5px;
  position: relative;
  width: 100%;
}

.results-wrapper:after {
  border-bottom: 13px solid transparent;
  border-right: 31px solid black;
  border-top: 13px solid transparent;
  content: "";
  height: 0;
  position: absolute;
  right: 99%;
  top: 26px;
  width: 0;
}

.results-wrapper:before {
  border-bottom: 14px solid transparent;
  border-right: 31px solid white;
  border-top: 13px solid transparent;
  content: "";
  height: 0;
  position: absolute;
  right: 100%;
  top: 26px;
  width: 0;
}



